/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect } from "react";
import { Editor } from "@tinymce/tinymce-react";
import { withFormik } from "formik";
import * as Yup from "yup";
import { connect, useSelector, useDispatch } from "react-redux";
import {
  CREATE_PROJECT,
  GET_ALL_PROJECT_CATEGORY_SAGA,
} from "../../../redux/constants/Cyberbugs/Cyberbugs";

function CreateProject(props) {
  const arrCategory = useSelector((state) => state.CategoryReducer.arrCategory);
  const dispatch = useDispatch();

  const { handleChange, handleSubmit, setFieldValue } = props;

  useEffect(() => {
    dispatch({ type: GET_ALL_PROJECT_CATEGORY_SAGA });
  }, [dispatch]);

  const handleEditorChange = (content, editor) => {
    setFieldValue("description", content);
  };

  return (
    <div className="container mt-5">
      <h3 className="text-primary">Create New Project</h3>
      <form onSubmit={handleSubmit} onChange={handleChange}>
        <div className="form-group">
          <p>Name</p>
          <input className="form-control" name="projectName" />
        </div>
        <div className="form-group">
          <p>Description</p>
          <Editor
            name="description"
            init={{
              selector: "textarea#myTextArea",

              height: 500,
              menubar: false,
              plugins: [
                "advlist autolink lists link image charmap print preview anchor",
                "searchreplace visualblocks code fullscreen",
                "insertdatetime media table paste code help wordcount",
              ],
              toolbar:
                "undo redo | formatselect | bold italic backcolor |alignleft aligncenter alignright alignjustify |  bullist numlist outdent indent | removeformat | help",
            }}
            onEditorChange={handleEditorChange}
          />
        </div>
        <div className="form-group">
          <select
            name="categoryId"
            className="form-control"
            onChange={handleChange}
          >
            {arrCategory.map((item, index) => {
              return (
                <option value={item.id} key={index}>
                  {item.projectCategoryName}
                </option>
              );
            })}
          </select>
        </div>
        <button className="btn btn-outline-primary w-100 mt-3" type="submit">
          Create project
        </button>
      </form>
    </div>
  );
}

const createProjectForm = withFormik({
  enableReinitialize: true,

  mapPropsToValues: (props) => ({
    projectName: "",
    description: "",
    categoryId: props.arrCategory[0]?.id,
  }),
  validationSchema: Yup.object().shape({}),
  handleSubmit: (values, { props, setSubmitting }) => {
    props.dispatch({
      type: CREATE_PROJECT,
      newProject: values,
    });
  },
  displayName: "CreateProjectFormik",
})(CreateProject);

const mapStateToProps = (state) => ({
  arrCategory: state.CategoryReducer.arrCategory,
});

export default connect(mapStateToProps)(createProjectForm);
